.video-react-forward-control::before {
  color: #fff;
  display: block;
  font-size: 15px !important;
}

.video-react-forward-control:nth-child(2)::before {
  content: '-5';
}

.video-react-forward-control:nth-child(3)::before {
  content: '-1';
}

.video-react-forward-control:nth-child(4)::before {
  content: '+1';
}

.video-react-forward-control:nth-child(5)::before {
  content: '+5';
}



